<template>
  <div id="app" class="page municipio">
    <Loading :hide="hide"></Loading>
    <div class="container">
      <div class="page-content">
        <section class="showcase box" v-if="emenda">
          <h2 class="mb-3">{{emenda.proposta.descricaoObjeto}}</h2>
          <h4 class="mb-2"><strong>Proponente:</strong> {{emenda.proposta.nomeProponente}}</h4>
          <h4 class="mb-2"><strong>Parlamentar:</strong> {{emenda.nomeParlamentar}}</h4>
          <div class="dadosEmenda mb-3">
            <div class="repasse help">
              <span class="title">Repasse</span>
              <span class="value">R$ {{currencyFormat(emenda.proposta.valorRepasseProposta)}}</span>
            </div>
            <div class="contrapartida help">
              <span class="title">Contrapartida</span>
              <span class="value">R$ {{currencyFormat(emenda.proposta.valorContrapartidaProposta)}}</span>
            </div>
            <div class="proposta help">
              <span class="title">Valor Global</span>
              <span class="value">R$ {{currencyFormat(emenda.proposta.valorGlobalProposta)}}</span>
            </div>
          </div>
          <div class="graficos">
            <div class="box box-invert mb-4">
              <h4 class="card-title">Composição do Valor Total</h4>
              <PieChart
                :data="mountChartData(emenda)"
                :options="{responsive: false, maintainAspectRatio: true}"
                :height="400">
              </PieChart>
            </div>
          </div>
        </section>
        <section v-else>
          nada :(
        </section>
      </div>
    </div>
  </div>
</template>

<script>
  import Loading from '@/components/Loading.vue'
  import PieChart from '@/components/PieChart.vue'

  export default {
    name: 'emenda',
    components: {
      Loading,
      PieChart
    },
    props: {
      id: String,
    },
    data: function () {
      return {
        emenda: false,
        hide: '',
      }
    }, methods: {
      getEmenda: function(id) {
        this.$http.get( this.$apiUrls.getEmendaPorId(id) ).then((response) => {
          this.emenda = response.data
          this.hide = 'hide'
        }, (error) => {
          this.hide = 'hide'
          console.log("Nao foi possivel receber emenda ")
          console.log(error)
          return false
        });
        return true;
      },
      mountChartData: function(emenda) {
        return {
          datasets: [{
              label: 'Valor Total: R$ '+this.currencyFormat(emenda.proposta.valorGlobalProposta),
              data: [emenda.proposta.valorRepasseProposta, emenda.proposta.valorContrapartidaProposta],
              backgroundColor: ['#ff0033', '#0022ff']
          }],
          labels: [
              'Repasse',
              'Contrapartida'
          ]
        };
      },
      currencyFormat(value) {
        let val = (value/1).toFixed(2).replace('.', ',')
        return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
      } 
    },
    mounted: function() {
      this.getEmenda(this.id)
    }
  }
</script>

<style scoped>
  .pull-right {
    float: right;
    text-align: right;  
  }
  .dadosEmenda {
    display: grid;
    grid-gap: 1em;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100%;
    font-size: 1.3rem;
  }
  .dadosEmenda > div {
    text-align: center;
    display: flex;
    flex-direction: column
  }
  .dadosEmenda > div > span{
    display: block;
  }
  .title {
    color: var(--secondary-color);
  }
</style>
